import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    redirect: '/dashboard',
  },
  {
    path: '/dashboard',
    component: () => import('@/views/Dashboard.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/analytics',
    component: () => import('@/views/Analytics.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/users',
    component: () => import('@/views/Users.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/users/:id',
    name: 'users',
    component: () => import('@/views/User.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/chat',
    component: () => import('@/views/Chat.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/withdraws',
    component: () => import('@/views/Withdraws.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/tools',
    component: () => import('@/views/Tools.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/antifraud',
    component: () => import('@/views/Antifraud.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/notifications',
    component: () => import('@/views/Notifications.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/referrals',
    component: () => import('@/views/Referrals.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/admin',
    component: () => import('@/views/Admin.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/tasks',
    component: () => import('@/views/Tasks.vue'),
    meta: {
      layout: 'main',
      requiresAuth: true,
    },
  },
  {
    path: '/auth',
    component: () => import('@/views/Auth.vue'),
    meta: {
      layout: 'empty',
      requiresAuth: false,
    },
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem('token')) {
        next('/');
      } else {
        next();
      }
    },
  },
  {
    path: '/logout',
    meta: {
      layout: 'empty',
      requiresAuth: true,
    },
    beforeEnter: (to, from, next) => {
      localStorage.removeItem('token');
      next('/auth');
    },
  },
  {
    path: '*',
    redirect: '/',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (!localStorage.getItem('token')) {
      next('auth');
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router;
