import ApolloClient from 'apollo-boost';

const apolloClient = new ApolloClient({
  uri: 'http://198.27.81.88/graphql',
  request: operation => {
    const token = localStorage.getItem('token');
    operation.setContext({
      headers: {
        authorization: token || '',
      },
    });
  },
});

export default apolloClient;
